import React from 'react';
import {Button, Card, Form, Select} from 'antd';

const constructionOptions  = [
    { label: '', value: '' },
    { label: ' ', value: ' ' },
    { label: ' ', value: ' ' },
    { label: '', value: '' },
    { label: '', value: '' },
    { label: ' ', value: ' ' },
];

const massOptions = [
    { label: '100-300 ', value: '100-300' },
    { label: '300-500 ', value: '300-500' },
    { label: '500-1', value: '500-1000' },
    { label: ' ', value: ' ' },
];

const detailsOptions = [
    { label: '1-5', value: '1-5' },
    { label: '5-10', value: '5-10' },
    { label: ' ', value: ' ' },
];

const brigadaOptions = [
    { label: '1', value: '1' },
    { label: '2', value: '2' },
    { label: '3', value: '3' },
    { label: '', value: '' },
];

const peopleOptions = [
    { label: '1', value: '1' },
    { label: '2', value: '2' },
    { label: '3', value: '3' },
    { label: '', value: '' },
];

const MakeNewCalculation: React.FC = () => {
    const [form] = Form.useForm();

    const handleFormSubmit = (values: any) => {
        //        
        console.log(values);
    };

    return (
        <Card>
        <Form style={{width: '500px'}} form={form} onFinish={handleFormSubmit}>
            <Form.Item name="select1" label="  ">
                <Select options={constructionOptions} placeholder={" "}/>
            </Form.Item>

            <Form.Item name="select2" label=":">
                <Select options={massOptions} placeholder={" "}/>
            </Form.Item>

            <Form.Item name="select3" label=" - ">
                <Select options={detailsOptions} placeholder={"- "}/>
            </Form.Item>

            <Form.Item name="select4" label=" ">
                <Select options={brigadaOptions} placeholder={""}/>
            </Form.Item>

            <Form.Item name="select5" label="- ">
                <Select options={peopleOptions} placeholder={"- "}/>
            </Form.Item>

            <Form.Item>
                <Button type="default" htmlType="submit"></Button>
            </Form.Item>
        </Form>
        </Card>
    );
};

export default MakeNewCalculation;
